.header {
	background: url('../img/header-bg.jpg?v=1.1.26') 50% 50% / cover, #142139;
	position: relative;
	z-index: 1;
	padding-top: 185px;
	padding-bottom: 85px;
	color: #fff;

	@media (max-width: 991px) {
		padding-bottom: 15px;
		padding-top: 80px;
	}

	@media (max-width: 767px) {
		padding-top: 70px;
	}

	&-title {
		font-size: 40.5px;
		line-height: 1.2;
		text-transform: uppercase;

		@media (max-width: 1199px) {
			font-size: 35.5px;
		}

		@media (max-width: 575px) {
			font-size: 7vw;
		}

		img {
			max-width: 100%;
		}

		.line {

			&-1{}
			&-2{}
			&-3{
				letter-spacing: 1.3px;
				margin-bottom: 2.5rem;

				@media (max-width: 1199px) {
					margin-bottom: 1.5rem;
				}

			}

		}
	}

	.btn {
		font-family: 'Roboto Condensed';
		font-size: 18px;
		font-weight: 300;
	}

	&-image {
		margin-top: -25px;

		 -webkit-perspective: 1200;
  perspective: 1200;
  -moz-transform: perspective(1200px);
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d;

		img {
			max-width: 525px;
			position: relative;
			transition: all .3s;

			-webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d;
		}
	}

	.particles {
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		z-index: 0;
	}



}

